* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  font-size: 62.5%; /* 1rem = 10px */
}

body {
  line-height: 1.5;
  font-family: sans-serif;
}

a {
  color: inherit;
  text-decoration: none;
}

ul li, ol li {
  list-style: none;
}

img, video {
  max-width: 100%;
  height: auto;
}

button {
  font-family: inherit;
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
}

input, textarea, select {
  font-family: inherit;
  border: none;
  outline: none;
}

body {
  font-family: "Nunito", sans-serif;
  font-size: 16px;
  color: var(--text-color);
  background-color: var(--background-color);
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
  color: var(--heading-color);
  margin-bottom: 1rem;
}

p {
  margin-bottom: 1rem;
}

ul, ol {
  list-style: none;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}